export class Tags{
    public id: number;
    public tagName: string;
    public tagDescription: string;
    constructor(tagName: string,tagDescription: string
    ){
        this.tagDescription = tagDescription;
        this.tagName = tagName;
    }
}

export class Sector{
    public id: number;
    public name: string;
    public code: string;
    public description: string;
    constructor(name: string,code:string,description: string
    ){
        this.description = description;
        this.name = name;
        this.code = code;
    }
}

export class SubSector{
    public id: number;
    public name: string;
    public code: string;
    public description: string;
    public sectorId: number;
    constructor(name: string,code:string,description: string,sectorId: number
    ){
        this.description = description;
        this.name = name;
        this.code = code;
        this.sectorId = sectorId;
    }
}

