import { Component, OnInit, ViewChild, AfterViewInit } from '@angular/core';
import { MatTableDataSource, MatSort, MatPaginator, MatSnackBar } from '@angular/material';
import * as csv from 'csvtojson';
import { ProjectService } from '../../../../project/service/project.service';
export interface GeoDivision {
  name: string;
  code: string;
  desc: string;
}

@Component({
  selector: 'app-division-masterdata',
  templateUrl: './division-masterdata.component.html',
  styleUrls: ['./division-masterdata.component.css']
})
export class DivisionMasterdataComponent implements OnInit,AfterViewInit {

  displayedColumns: string[] = ['sn', 'name','code','desc'];
  public dataSource = new MatTableDataSource<GeoDivision>();
  uploadFileName = 'No file Chosen'
  selectedFile :string;
  @ViewChild(MatSort) sort: MatSort;
  @ViewChild(MatPaginator) paginator: MatPaginator;

  uploadDataSource : GeoDivision[];

  constructor(public projectService: ProjectService,
              private snackBar: MatSnackBar) { }

  ngOnInit() {
  }

  ngAfterViewInit(): void {
    this.dataSource.sort = this.sort;
    this.dataSource.paginator = this.paginator;
  }

  public selectFile(event) {
    this.onFileSelected();
  }

  public onFileSelected() {

    const $pdf: any = document.querySelector('#file');

    if (typeof FileReader !== 'undefined') {
      const reader = new FileReader();

      reader.onload = (e: any) => {
        this.selectedFile = e.target.result;
      };

      this.uploadFileName = $pdf.files[0].name;
      console.log(this.uploadFileName);
      reader.readAsText($pdf.files[0]);
    }



  }

  public parseData() {
    csv().fromString(this.selectedFile)
      .then((res) => {
        console.log(res);
        this.dataSource.data = res as GeoDivision[];
        this.uploadDataSource = res;
      })
  }

  public uploadData(){
    //console.log(this.dataSource.data);

    this.projectService.uploadGEODivision(this.uploadDataSource).subscribe(
      data => {
        console.log(data);
        this.dataSource.data = [];
        this.uploadDataSource = null;
        this.openSnackBar(data.message, '', 'blue-snackbar');
      },
      error => {
        console.log(error);
        this.openSnackBar(error.error, '', 'red-snackbar');
      }
    );



  }

  openSnackBar(message: string, action: string, className: string) {
    this.snackBar.open(message, action,
      {
        duration: 2000,
        verticalPosition: 'bottom',
        horizontalPosition: 'end',
        panelClass: [className]
      });
  }

}
