import { Component, OnInit, Input } from '@angular/core';
import {SelectionModel} from '@angular/cdk/collections';
import {MatTableDataSource, MatDialogRef, MatSnackBar} from '@angular/material';
import { ProjectService } from '../../../../project/service/project.service';
import { Division, District, Upazila } from '../../../../select-location-dialog/interfaces';
import { AddSpecialLocationModel, SpecialLocation } from './add-special-location-model';

@Component({
  selector: 'app-add-special-location',
  templateUrl: './add-special-location.component.html',
  styleUrls: ['./add-special-location.component.css']
})
export class AddSpecialLocationComponent implements OnInit {

  selection = new SelectionModel<Upazila>(true, []);

  @Input() spLocName:any;
  @Input() definitionGranularity:any;
  @Input() code:any;
  @Input() desc:any;
  @Input() selectedDivision:any;
  @Input() selectedDistrict:any;
  divisions: Division[];
  districts : District[] = [];
  upazilas : Upazila[] = [];
  lastId:string;
  tempLastDivId:string;
  tempLastDisId:string;
  submitMessage = '';
  specialLocation : AddSpecialLocationModel;
  spLoc: SpecialLocation;


  constructor(
    public projectService: ProjectService,
    private snackBar: MatSnackBar) 
    { }

  ngOnInit() {
    this.getDivision();
  }

  /** Whether the number of selected elements matches the total number of rows. */
  isAllSelected() {
    const numSelected = this.selection.selected.length;
    const numRows = this.upazilas.length;
    return numSelected === numRows;
  }

  /** Selects all rows if they are not all selected; otherwise clear selection. */
  masterToggle() {
    this.isAllSelected() ?
        this.selection.clear() :
        this.upazilas.forEach(row => 
          this.selection.select(row)
        );
    console.log("Selected Upazila => "+JSON.stringify(this.selection.selected));
  }

  onSubmit(){

    //console.log("Selected Upazila => "+JSON.stringify(this.selection.selected));
    this.spLoc = new SpecialLocation(this.spLocName,null,null,0);
    this.specialLocation = new AddSpecialLocationModel(
      this.selection.selected,
      this.spLoc,
      this.selectedDistrict,
      this.selectedDivision
    );

    console.log("Selected Upazila => "+JSON.stringify(this.specialLocation));

    this.projectService.addSpecialLoc(this.specialLocation).subscribe(
      data => {
        //console.log(data);
        this.submitMessage = data.message;
        this.openSnackBar(this.submitMessage, '', 'blue-snackbar');
        //window.location.reload();
        this.clearFormData();
      },
      error => {
        console.log(error);
        this.submitMessage = error.error;
        this.openSnackBar(this.submitMessage, '', 'red-snackbar');

      }
    );


  }
  clearFormData() {
    this.districts = [];
    this.upazilas = [],
    this.selectedDistrict = [];
    this.selectedDivision = [];
    this.selection.clear();
    this.spLocName = '';
    this.code = '';
    this.desc = '',
    this.definitionGranularity = 0;
  }

  close() {
    this.clearFormData();
  }

  public getDivision(){
    this.projectService.getLocationDivision().subscribe((data : Division[])=>{
      this.divisions = data;
    });
  }

  public getDistrict(id: string){
    if(id != undefined){
      if(id.length > 1){
        this.lastId = id[id.length-1];
      }
      else{
        this.lastId = id[0];
      }
      if(this.tempLastDivId !== this.lastId){
        this.projectService.getDistrict(this.lastId).subscribe((data: District[]) =>{
          this.tempLastDivId = this.lastId;
          if(data.entries){
            data.forEach(element => {
              if(id.length == 1){
                this.districts = data;
              }else{
                this.districts.push(element);
                this.districts = this.districts.slice();
              }
            });
            //this.districts = data;
          }
        });
      }
    }
  }

  public getUpazila(id: string){
    if(id != undefined){
      if(id.length > 1){
        this.lastId = id[id.length-1];
      }
      else{
        this.lastId = id[0];
      }

      if(this.tempLastDisId !== this.lastId){

        this.projectService.getUpazila(this.lastId).subscribe((data : Upazila[]) =>{
          this.tempLastDisId = this.lastId;
          if(data.entries){
            data.forEach(element => {
              if(id.length == 1){
                this.upazilas = data;
              }else{
                this.upazilas.push(element);
                this.upazilas = this.upazilas.slice();
              }
            });
          }
        });
      }

    }
  }

  onRemoveDistrict($event) {
    this.districts = this.districts.splice(this.districts.indexOf($event.value.id), 0);

    //this.districts = this.districts.filter(item => item.id !== $event.value.id);
  }
  onRemoveUpazila($event) {
    this.upazilas = this.upazilas.splice(this.upazilas.indexOf($event.value.id), 0);
    //this.upazilas = this.upazilas.filter(item => item.id !== $event.value.id);
  }

  public openSnackBar(message: string, action: string, className: string) {
    this.snackBar.open(message, action,
      {
        duration: 2000,
        verticalPosition: 'bottom',
        horizontalPosition: 'end',
        panelClass: [className]
      });
  }

}
