import { Component, OnInit } from '@angular/core';
import { MatDialogRef } from '@angular/material';
import { Tags } from '../data-acquisition/model/interfaces';

@Component({
  selector: 'app-add-tag-dialog',
  templateUrl: './add-tag-dialog.component.html',
  styleUrls: ['./add-tag-dialog.component.css']
})
export class AddTagDialogComponent implements OnInit {

  constructor(private dialogRef: MatDialogRef<AddTagDialogComponent>) { }

  tags: Tags;

  ngOnInit() {
    this.tags = new Tags('','');
  }

  onSubmit(){
    this.dialogRef.close(this.tags);
  }

  close() {
    this.dialogRef.close();
  }

}
