import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable, BehaviorSubject } from 'rxjs';

import { JwtResponse } from './jwt-response';
import { AuthLoginInfo } from './login-info';
import { SignUpInfo } from './signup-info';
import { Router } from '@angular/router';
import { NetworkConstant } from '../constants/network-constant';
import { ResponseMessage } from '../dashboard/response-message/response-message';

const httpOptions = {
  headers: new HttpHeaders({ 'Content-Type': 'application/json' })
};

@Injectable({
  providedIn: 'root'
})
export class AuthService {

  private loginUrl = NetworkConstant.BASE_URL+NetworkConstant.LOGIN_ENDPOINT;
  private signupUrl = NetworkConstant.BASE_URL+NetworkConstant.SIGNUP_ENDPOINT;

  private loggedIn = new BehaviorSubject<boolean>(false); // {1}
  
  constructor(private http: HttpClient,private router:Router) {
  }

  get isLoggedIn() {
    return this.loggedIn.asObservable(); // {2}
  }

  attemptAuth(credentials: AuthLoginInfo): Observable<JwtResponse> {
    //this.loggedIn.next(true);
    //this.router.navigate(['/dashboard']);
    return this.http.post<JwtResponse>(this.loginUrl, credentials, httpOptions);
  }

  signUp(info: SignUpInfo): Observable<ResponseMessage> {
    return this.http.post<ResponseMessage>(this.signupUrl, info, httpOptions);
  }
}
