  public insertData(){

    console.log("mngjdfg"+this.selectedProjectId.projectid.length);

    if (this.selectedProjectId.projectid.length === 0) { 
      this.openSnackBar('Input filed can not be empty!', '', 'red-snackbar');
    } else {

      this.loader = true;

      console.log(JSON.stringify(this.dataAcquisition));

      this.projectService.submitParsedData(this.dataAcquisition).subscribe(
        data => {
          //console.log(data);
          this.submitMessage = data.message;
          this.openSnackBar(this.submitMessage, '', 'blue-snackbar');
          this.loader = false;
          //window.location.reload();
          this.clearFormData();
        },
        error => {
          console.log(error);
          this.submitMessage = error.error;
          this.openSnackBar(this.submitMessage, '', 'red-snackbar');
          this.loader = false;
        }
      );
      
    }
  }

  public updateData(){
    if (this.selectedProjectId.length === 0) { 
      this.openSnackBar('Input filed can not be empty!', '', 'red-snackbar');
    } else {
      this.loader = true;
      console.log(JSON.stringify(this.dataAcquisition));

      this.projectService.updateProjectInfo(this.dataAcquisition).subscribe(
        data => {
          console.log(data);
          this.submitMessage = data.message;
          this.openSnackBar(this.submitMessage, '', 'blue-snackbar');
          this.loader = false;
          this.clearFormData();
        },
        error => {
          console.log(error);
          this.submitMessage = error.error;
          this.openSnackBar(this.submitMessage, '', 'red-snackbar');
          this.loader = false;
        }
      );
      
    }
  }


