  public openAddTagDialog(tagType : string){
    const dialogRef = this.dialog.open(AddTagDialogComponent,{
      width: '60%',
      hasBackdrop: false,
      
    });

    dialogRef.afterClosed().subscribe(
      (data : Tags) => {
        //console.log("Upazila Data => "+data);
        if (!data) {

        } else {

          if(tagType === 'tag'){
            this.createTags(data);
          }
          else if(tagType === 'locationTag'){
            this.createLocationTag(data);
          }
          else if(tagType === 'sectorTag'){
            this.createSectorTag(data);
          }
          
          console.log("Upazila Data => "+ JSON.stringify(data));
        
        }

      }
    )
  }

