import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA, MatSnackBar } from '@angular/material';
import { ProjectInfo } from '../../project/model/project-info';
import { DatePipe } from '@angular/common';
import { TokenStorageService } from '../../../auth/token-storage.service';

@Component({
  selector: 'app-create-project',
  templateUrl: './create-project.component.html',
  styleUrls: ['./create-project.component.css'],
  providers: [DatePipe]
})

export class CreateProjectComponent implements OnInit {
  dateNow: Date = new Date();
  projectId = this.dateNow.getTime();
  todaysDate : string;
  
  projectInfo : ProjectInfo;

  constructor(
    private datePipe: DatePipe,
    private tokenStorage : TokenStorageService,
    private dialogRef: MatDialogRef<CreateProjectComponent>) {
  }

  ngOnInit() {
    this.todaysDate = this.datePipe.transform(this.dateNow, 'dd-MM-yyyy HH:mm:ss');
    this.projectInfo = new ProjectInfo(''+this.projectId, '',this.tokenStorage.getUsername(),this.todaysDate, null);
    console.log(this.todaysDate);
  }

  onSubmit(){
    this.dialogRef.close(this.projectInfo);
  }

  close() {
    this.dialogRef.close();
  }
}
