import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AuthGuard } from './guard/auth.guard';
import { HomeComponent } from './dashboard/home/home.component';
import { DataAcquisitionComponent } from './dashboard/data-acquisition/data-acquisition.component';
import { ReportsComponent } from './dashboard/reports/reports.component';
import { ProjectComponent } from './dashboard/project/project.component';
import { LoginComponent } from './login/login.component';
import { DashboardComponent } from './dashboard/dashboard.component';
import { RegisterComponent } from './dashboard/register/register.component';
import { MasterDataConfigComponent } from './dashboard/master-data-config/master-data-config.component';
import { SelectLocationDialogComponent } from './dashboard/select-location-dialog/select-location-dialog.component';
import { FileViewerComponent } from './dashboard/file-viewer/file-viewer.component';
const routes: Routes = [
  {
    path: '',
    redirectTo: '/dashboard',
    pathMatch: 'full',
    canActivate: [AuthGuard]

  },
  {
    path: 'dashboard',
    component: DashboardComponent,
    canActivate: [AuthGuard],
    children: [
      {
        path: '',
        redirectTo: 'projects',
        pathMatch: 'full'
      },
      {
        path: 'projects',
        component: ProjectComponent,
        canActivate: [AuthGuard],
        runGuardsAndResolvers: 'always',
      },
      {
        path: 'data-acquisition',
        component: DataAcquisitionComponent,
        canActivate: [AuthGuard],
        runGuardsAndResolvers: 'always',
      },
      {
        path: 'data-management',
        component: DataAcquisitionComponent,
        canActivate: [AuthGuard],
        runGuardsAndResolvers: 'always',
      },
      {
        path: 'generate-reports',
        component: ReportsComponent,
        canActivate: [AuthGuard],
        runGuardsAndResolvers: 'always',
      },
      {
        path: 'projects',
        component: ProjectComponent,
        canActivate: [AuthGuard],
        runGuardsAndResolvers: 'always',
      },
      {
        path: 'view-document/:docUrl',
        component: FileViewerComponent,
        canActivate: [AuthGuard],
        runGuardsAndResolvers: 'always',
      },
      {
        path: 'master-data-config',
        component: MasterDataConfigComponent,
        canActivate: [AuthGuard]
      },
      {
        path: 'register',
        component: RegisterComponent,
        canActivate: [AuthGuard],
        runGuardsAndResolvers: 'always',
      }
    ]
  },
  {
    path: 'login',
    component: LoginComponent
  },
  {
    path: '**',
    redirectTo: ''
  }
];

@NgModule({
  imports: [RouterModule.forRoot(
    routes,{ useHash: true , onSameUrlNavigation: 'reload'},
  )],
  exports: [RouterModule]
})
export class AppRoutingModule {}
