import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { NetworkConstant } from '../constants/network-constant';
import { ResponseMessage } from '../dashboard/response-message/response-message';

@Injectable({
  providedIn: 'root'
})
export class UserService {

  private getAllUserUrl = NetworkConstant.BASE_URL+NetworkConstant.GET_USER_LIST_ENDPOINT;
  private updateUserUrl = NetworkConstant.BASE_URL+NetworkConstant.UPDATE_USER_INFO_ENDPOINT;
  private deleteUserUrl = NetworkConstant.BASE_URL+NetworkConstant.DELETE_USER_INFO_ENDPOINT;

  constructor(private http: HttpClient) { }

  public getUserList(){
    return this.http.get(this.getAllUserUrl);
  }

  public deleteUser(userName:string):Observable<ResponseMessage>{
    return this.http.delete<ResponseMessage>(this.deleteUserUrl+userName);
  }
}
