import { Component, OnInit, ViewChild} from '@angular/core';
import { ChartOptions, ChartType, ChartDataSets } from 'chart.js';
import * as pluginDataLabels from 'chartjs-plugin-datalabels';
import { Label, SingleDataSet, Color, BaseChartDirective } from 'ng2-charts';
import { ProjectService } from '../project/service/project.service';
import { ProjectInfo } from '../project/model/project-info';
@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {

  noOfProject : number;
   // Pie
  public pieChartOptions: ChartOptions = {
    responsive: true,
    legend: {
      position: 'top',
    },
    plugins: {
      datalabels: {
        formatter: (value, ctx) => {
          const label = ctx.chart.data.labels[ctx.dataIndex];
          return label;
        },
      },
    }
  };
  public pieChartLabels: Label[] = ['ভৌত অবকাঠামো বিভাগ', 'শিল্প ও শক্তি বিভাগ', 'আর্থ সামাজিক অবকাঠামো বিভাগ', 'কার্যক্রম বিভাগ'];
  /*public pieChartLabels: Label[] = ['ঢাকা', 'চট্টগ্রাম', 'বরিশাল','সিলেট','রাজশাহী','খুলনা','রংপুর','ময়মনসিংহ'];*/
  public pieChartData: number[] = [300, 500, 100,200];
  public pieChartType: ChartType = 'pie';
  public pieChartLegend = true;
  public pieChartPlugins = [pluginDataLabels];
  public pieChartColors = [
    {
      backgroundColor: ['rgba(255,0,0,0.3)', 'rgba(0,255,0,0.3)', 'rgba(0,0,255,0.3)', '#fb9678', '#03a9f3'],
    },
  ];

  /*bar chart start*/
  public barChartOptions: ChartOptions = {
    responsive: true,
    // We use these empty structures as placeholders for dynamic theming.
    scales: { xAxes: [{}], yAxes: [{}] },
    plugins: {
      datalabels: {
        anchor: 'end',
        align: 'end',
      }
    }
  };
  public barChartLabels: Label[] = ['2006', '2007', '2008', '2009', '2010', '2011', '2012'];
  public barChartType: ChartType = 'bar';
  public barChartLegend = true;
  public barChartPlugins = [pluginDataLabels];

  public barChartData: ChartDataSets[] = [
    { data: [65, 59, 80, 81, 56, 57, 70], label: 'Yearwise Project' },
    
  ];
// bar chart end

  constructor(public projectService: ProjectService) { }

  ngOnInit() {
    this.getAllProject();
  }

  public getAllProject() {
    this.projectService.getAllProject().subscribe((res : ProjectInfo[]) => {
      this.noOfProject = res.length;
    });
  }
  /* events*/
  public chartClicked({ event, active }: { event: MouseEvent, active: {}[] }): void {
    console.log(event, active);
  }

  public chartHovered({ event, active }: { event: MouseEvent, active: {}[] }): void {
    console.log(event, active);
  }
}
