    constructor(private http: HttpClient) { }

    public createproject(info: ProjectInfo): Observable<ResponseMessage> {
        return this.http.post<ResponseMessage>(this.createProjectUrl, info, httpOptions);
    }

    public getAllProject() {
        return this.http.get(this.getAllProjectUrl);
    }

    public getParsedData(info: ParseDataPostModel) {
        return this.http.post(this.parseDataUrl, info, httpOptions);
    }


    public convertDocxToPdf(info: ConvertDocxToPdfModel) {
        return this.http.post(this.convertDocxToPdfUrl, info, httpOptions);
    }


    public submitParsedData(info: Summery): Observable<ResponseMessage> {
        return this.http.post<ResponseMessage>(this.saveParseDataUrl, info, httpOptions);
    }
    // Save History
    public saveAsHostory(info: ProjectHistory): Observable<ResponseMessage> {
        return this.http.post<ResponseMessage>(this.saveAsHistoryUrl, info, httpOptions);
    }

    public getProjectInfo(projectId: string) {
        console.log(this.getProjectInfoUrl + projectId);
        return this.http.get(this.getProjectInfoUrl + projectId);
    }

    public getProjectOperationalInfo(projectId: string) {
        console.log(this.getOperationalProjectInfoUrl + projectId);
        return this.http.get(this.getOperationalProjectInfoUrl + projectId);
    }

    public updateProjectInfo(info: Summery): Observable<ResponseMessage> {
        console.log(this.updateProjectInfoUrl);
        return this.http.put<ResponseMessage>(this.updateProjectInfoUrl, info, httpOptions);
    }

    public deleteProjectInfo(info: Summery): Observable<ResponseMessage> {
        const httpOptions = {
            headers: new HttpHeaders({ 'Content-Type': 'application/json' }),
            body: info
        };
        return this.http.delete<ResponseMessage>(this.deleteProjectInfoUrl, httpOptions);
    }

