import { Component, OnInit, ViewChild, AfterViewInit, Input } from '@angular/core';
import { MatSnackBar, MatDialog, MatDialogConfig, MatTableDataSource, MatPaginator, MatSort } from '@angular/material';
import { ProjectService } from './service/project.service';
import { CreateProjectComponent } from './create-project/create-project.component';
import { ProjectInfo } from './model/project-info';
import { ConfirmationDialogComponent } from '../confirmation-dialog/confirmation-dialog.component';
import { Ng4LoadingSpinnerService } from 'ng4-loading-spinner';
import { DocReferences } from '../data-acquisition/model/doc-reference';
import { UploadFileService } from '../../services/upload-file-service.service';
import { HttpEventType, HttpResponse, HttpErrorResponse } from '@angular/common/http';
import { Router, ActivatedRoute } from '@angular/router';
import { FileViewerComponent } from '../file-viewer/file-viewer.component';
import { ConvertDocxToPdfModel } from '../data-acquisition/model/convert-docx-to-pdf-model';


@Component({
  selector: 'app-project',
  templateUrl: './project.component.html',
  styleUrls: ['./project.component.css']
})

export class ProjectComponent implements OnInit, AfterViewInit {
  id: any;
  isOpen: boolean;
  description: any;
  message = '';
  docReference: DocReferences[];
  @Input() meetingDate:any;

  progress: { percentage: number } = { percentage: 0 }
  selectedFiles: FileList;
  currentFileUpload: File;
  fileUploadSuccess = false;
  public displayedColumns = ['searialNo','projectId', 'projectName','dpp','mm','summary', 'date', 'delete'];
  public dataSource = new MatTableDataSource<ProjectInfo>();
  convertDocxToPdfModel : ConvertDocxToPdfModel;

  @ViewChild(MatSort) sort: MatSort;
  @ViewChild(MatPaginator) paginator: MatPaginator;

  progressing: Boolean;

  constructor(
    private route: ActivatedRoute,
    private router: Router,
    public uploadService: UploadFileService,
    private spinnerService: Ng4LoadingSpinnerService,
    private snackBar: MatSnackBar,
    private projectService: ProjectService,
    private dialog: MatDialog) {
    this.isOpen = false;
    this.progressing = true;
  }

  ngOnInit() {

    this.getAllProject();
    this.getAllFileResource();

    this.spinnerService.show();

  }
  ngAfterViewInit(): void {
    this.dataSource.sort = this.sort;
    this.dataSource.paginator = this.paginator;
  }

  public getAllProject() {
    this.projectService.getAllProject().subscribe(res => {
      this.dataSource.data = res as ProjectInfo[];
      console.log("data => "+JSON.stringify(this.dataSource.data));
      //this.spinnerService.hide();
      this.progressing = false;
    });
  }

  public selectFile(event) {
    this.selectedFiles = event.target.files;
  }

  /****
   * @theReza
   * Upload File for Parsing
   */
  public upload(projectId: string, sourceType: string) {
    this.progress.percentage = 0;

    this.currentFileUpload = this.selectedFiles.item(0);
    let filename = this.selectedFiles.item(0).name
    console.log(this.selectedFiles.item(0).name);
    
    
    this.uploadService.pushFileToStorage(projectId, sourceType, this.currentFileUpload).subscribe(event => {
      if (event.type === HttpEventType.UploadProgress) {
        this.fileUploadSuccess = true;
        this.progress.percentage = Math.round(100 * event.loaded / event.total);
      } else if (event instanceof HttpResponse) {
        console.log('File is completely uploaded!');
        console.log(filename);
        this.fileUploadSuccess = true;
        this.getAllProject();
        this.getAllFileResource();
        this.convertDocxToPdf(filename,sourceType);
        this.currentFileUpload = undefined;
        

      }

    });


    this.selectedFiles = undefined;
  }

  public convertDocxToPdf(filename: string, folderName:string){

    console.log("Called -> ");
    
    switch (folderName) {
      case "summary":
          this.convertDocxToPdfModel = new ConvertDocxToPdfModel("summary",filename);
          console.log(this.convertDocxToPdfModel);
        break;
      case "dpp":
          this.convertDocxToPdfModel = new ConvertDocxToPdfModel("dpp",filename);
        break;
      case "mm":
          this.convertDocxToPdfModel = new ConvertDocxToPdfModel('meetingminute',filename);
        break;
    }
    this.projectService.convertDocxToPdf(this.convertDocxToPdfModel).subscribe(data => {
      console.log(data);
      console.log("Converted -> ");
    });
  }




  public isFilePresent(projectId: string, sourceType: string){

    for(let data of this.dataSource.data){
      if(data.projectid === projectId){
        for(let file of data.uploadedFile){
          if(file.sourceType === sourceType){
            return file.fileDownloadUri;
          }
        }
      }
    }
    return false;
  }

  public viewDocument(url : string){
    console.log(url);
    //window.open('view-document','_blank');
    //this.router.navigate(['view-document'], {relativeTo: this.route});
    this.openDialogFilePreview(url);
    
  }


  public getAllFileResource(){
    this.projectService.getAllFileResource().subscribe(data =>{
      this.docReference = data as DocReferences[];
      console.log(this.docReference);
    });
  }

  public redirectToDelete = (id: string) => {
    const dialogConfig = new MatDialogConfig();
    dialogConfig.disableClose = true;
    dialogConfig.data = {
      id: 1,
      title: 'Warning!',
      body:'Do you wish to delete this project?',
    };
    const dialogRef = this.dialog.open(ConfirmationDialogComponent, dialogConfig);
    dialogRef.afterClosed().subscribe(result => {
      console.log('Dialog was closed')
      console.log(result)
      if(result){
        this.projectService.deleteProject(id).subscribe(
          data=>{
            this.openSnackBar(data.message, 'Ok', 'blue-snackbar');
            this.getAllProject();
          },
          error => {
            this.openSnackBar(error.message, 'Ok', 'red-snackbar');
          }
        )
      }
    });
  }

  public doFilter = (value: string) => {
    this.dataSource.filter = value.trim().toLocaleLowerCase();
  }

  public removeFilter(){
    this.meetingDate = '';
    this.doFilter('');
  }
  
  public openDialog() {

    if (this.isOpen) {
      this.isOpen = false;
    }
    else {
      const createProjectDialogRef = this.dialog.open(CreateProjectComponent);
      this.isOpen = true;

      createProjectDialogRef.afterClosed().subscribe(
        data => {
          console.log(data);
          if (!data) {

          } else {
            this.createProject(data);
          }

        }
      )

    }
  }

  public openDialogFilePreview( url: string){

    // const dialogConfig = new MatDialogConfig();

    // dialogConfig.data = {
    //   fileUrl: url
    // };

    this.dialog.open(FileViewerComponent, {
      height: '80%',
      width: '50%',
      hasBackdrop: false,
      autoFocus : false,
      data:{fileUrl: url}
    });
  }

  createProject(projectInfo: ProjectInfo) {
    this.projectService.createproject(projectInfo).subscribe(
      data => {
        console.log(data);
        this.message = data.message;
        this.getAllProject();
        this.openSnackBar(this.message, '', 'blue-snackbar');
      },
      error => {
        console.log(error);
        this.message = error.error;
        this.openSnackBar(this.message, 'Ok', 'red-snackbar');
      }
    )
  }
  openSnackBar(message: string, action: string, className: string) {
    this.snackBar.open(message, action,
      {
        duration: 2000,
        verticalPosition: 'bottom',
        horizontalPosition: 'end',
        panelClass: [className]
      });
  }

}
