import { Component, OnInit, AfterViewInit, ViewChild } from '@angular/core';
import { NgForm } from '@angular/forms';
import { Tags } from '../../../../dashboard/data-acquisition/model/interfaces';
import { MatSnackBar, MatTableDataSource, MatSort, MatPaginator } from '@angular/material';
import { ProjectService } from '../../../project/service/project.service';

export interface SectorTag {
  tagName: string;
  tagDesc: string;
}
@Component({
  selector: 'app-add-sector-tags',
  templateUrl: './add-sector-tags.component.html',
  styleUrls: ['./add-sector-tags.component.css']
})
export class AddSectorTagsComponent implements OnInit,AfterViewInit {

  @ViewChild(MatSort) sort: MatSort;
  @ViewChild(MatPaginator) paginator: MatPaginator;
  
  constructor(
    private snackBar: MatSnackBar,
    private projectService: ProjectService,) { }

  displayedColumns: string[] = ['sn', 'tagName', 'tagDesc'];
  dataSource = new MatTableDataSource<Tags>();
  message ='';
  sectorTags: Tags;

  ngOnInit() {
    this.sectorTags = new Tags('','');
    this.getSectorTags();
  }

  ngAfterViewInit(): void {
    this.dataSource.sort = this.sort;
    this.dataSource.paginator = this.paginator;
  }

  onSubmit(form : NgForm){
    this.projectService.addSectorTags(this.sectorTags).subscribe(
      data => {
        console.log(data);
        this.message = data.message;
        this.getSectorTags();
        form.reset();
        this.openSnackBar(this.message, '', 'blue-snackbar');
      },
      error => {
        console.log(error);
        this.message = error.error;
        this.openSnackBar(this.message, 'Ok', 'red-snackbar');
      }
    )

  }

  public getSectorTags(){
    this.projectService.getSectorTags().subscribe(res => {
      this.dataSource.data = res as Tags[];

      console.log(res);
    });
  }

  openSnackBar(message: string, action: string, className: string) {
    this.snackBar.open(message, action,
      {
        duration: 2000,
        verticalPosition: 'bottom',
        horizontalPosition: 'end',
        panelClass: [className]
      });
  }

}
