import { Component, OnInit, Input } from '@angular/core';
import {SelectionModel} from '@angular/cdk/collections';
import {MatTableDataSource, MatDialogRef} from '@angular/material';
import { ProjectService } from '../project/service/project.service';
import { Division, District, Upazila } from './interfaces';
import { stringify } from '@angular/compiler/src/util';

export interface Tile {
  text: string;
}
@Component({
  selector: 'app-select-location-dialog',
  templateUrl: './select-location-dialog.component.html',
  styleUrls: ['./select-location-dialog.component.css']
})
export class SelectLocationDialogComponent implements OnInit {
  selection = new SelectionModel<Upazila>(true, []);
  
  divisions: Division[];
  districts : District[] = [];
  upazilas : Upazila[] = [];
  lastId:string;
  tempLastDivId:string;
  tempLastDisId:string;

  progressing: Boolean;

  @Input() selectedDivision:any;
  @Input() selectedDistrict:any;

  constructor(
    private dialogRef: MatDialogRef<SelectLocationDialogComponent>,
    public projectService: ProjectService) { 
      this.progressing = false;
    }

  ngOnInit() {

    this.getDivision();
  }
  
  /** Whether the number of selected elements matches the total number of rows. */
  isAllSelected() {
    const numSelected = this.selection.selected.length;
    const numRows = this.upazilas.length;
    return numSelected === numRows;
  }

  /** Selects all rows if they are not all selected; otherwise clear selection. */
  masterToggle() {
    this.isAllSelected() ?
        this.selection.clear() :
        this.upazilas.forEach(row => 
          this.selection.select(row)
        );
    console.log("Selected Upazila => "+JSON.stringify(this.selection.selected));
  }

  onSubmit(){

    if(this.isAllSelected){
      this.dialogRef.close(this.selection.selected);
    }
    else{
      this.dialogRef.close();
    }

  }

  close() {
    this.dialogRef.close();
  }

  public getDivision(){
    this.projectService.getLocationDivision().subscribe((data : Division[])=>{
      this.divisions = data;
    });
  }

  public getDistrict(id: string){
    if(id != undefined){
      if(id.length > 1){
        this.lastId = id[id.length-1];
      }
      else{
        this.lastId = id[0];
      }
      if(this.tempLastDivId !== this.lastId){
        this.projectService.getDistrict(this.lastId).subscribe((data: District[]) =>{
          this.tempLastDivId = this.lastId;
          if(data.entries){
            data.forEach(element => {
              if(id.length == 1){
                this.districts = data;
              }else{
                this.districts.push(element);
                this.districts = this.districts.slice();
              }
            });
            //this.districts = data;
          }
        });
      }
    }
  }

  public getUpazila(id: string){
    
    if(typeof id === 'undefined'){
      this.progressing = false;
    }
    else{
      
      if(id.length > 1){
        this.lastId = id[id.length-1];
      }
      else{
        this.lastId = id[0];
      }

      if(typeof id !== 'undefined' && this.tempLastDisId !== this.lastId){

        this.progressing = true;

        console.log("start ");

        this.projectService.getUpazila(this.lastId).subscribe((data : Upazila[]) =>{
          this.tempLastDisId = this.lastId;
          if(data.entries){
            data.forEach(element => {
              if(id.length == 1){
                this.upazilas = data;
              }else{
                this.upazilas.push(element);
                this.upazilas = this.upazilas.slice();
              }
            });
          }
          this.progressing = false;
        });
      }
    }
  }

  onRemoveDistrict($event) {
    this.districts = this.districts.splice(this.districts.indexOf($event.value.id), 0);

    //this.districts = this.districts.filter(item => item.id !== $event.value.id);
  }
  onRemoveUpazila($event) {
    this.upazilas = this.upazilas.splice(this.upazilas.indexOf($event.value.id), 0);
    //this.upazilas = this.upazilas.filter(item => item.id !== $event.value.id);
    this.progressing = false;

  }


}
