import { Component, OnInit, ViewChild, AfterViewInit } from '@angular/core';

import * as csv from 'csvtojson';
import { MatTableDataSource, MatSort, MatPaginator, MatSnackBar } from '@angular/material';
import { ProjectService } from '../../../project/service/project.service';
import { ExportToCsv } from 'export-to-csv';

export interface Ministry {
  ministryName: string;
  code: string;
  desc: string;
}

@Component({
  selector: 'app-ministry-upload',
  templateUrl: './ministry-upload.component.html',
  styleUrls: ['./ministry-upload.component.css']
})
export class MinistryUploadComponent implements OnInit,AfterViewInit {
  displayedColumns: string[] = ['sn', 'name','code','desc'];
  public dataSource = new MatTableDataSource<Ministry>();
  uploadFileName = 'No file Chosen'
  selectedFile :string;
  @ViewChild(MatSort) sort: MatSort;
  @ViewChild(MatPaginator) paginator: MatPaginator;

  uploadDataSource : Ministry[];

  constructor(public projectService: ProjectService,
    private snackBar: MatSnackBar,) { }

  ngOnInit() {

  }

  ngAfterViewInit(): void {
    this.dataSource.sort = this.sort;
    this.dataSource.paginator = this.paginator;
  }

  public selectFile(event) {
    this.onFileSelected();
  }

  public onFileSelected() {

    const $pdf: any = document.querySelector('#file');

    if (typeof FileReader !== 'undefined') {
      const reader = new FileReader();

      reader.onload = (e: any) => {
        this.selectedFile = e.target.result;
      };
      this.uploadFileName = $pdf.files[0].name;
      console.log(this.uploadFileName);
      reader.readAsText($pdf.files[0]);
    }



  }

  public parseData() {
    csv().fromString(this.selectedFile)
      .then((res) => {
        console.log(res);
        this.dataSource.data = res as Ministry[];
        this.uploadDataSource = res;

        console.log(this.uploadDataSource);
      })
  }

  downloadMinistryFormat() {

    var data = [
      {
        ministryName: '',
        code: '',
        desc: ''
      }
    ];
    const options = {
      fieldSeparator: ',',
      quoteStrings: '"',
      decimalSeparator: '.',
      showLabels: true,
      filename: 'ministry-format',
      useTextFile: false,
      useBom: true,
      useKeysAsHeaders: true,
    };
    const csvExporter = new ExportToCsv(options);
    csvExporter.generateCsv(data);
  }


  public uploadData(){
    //console.log(this.dataSource.data);

    if(this.dataSource.data.length === 0){
      this.openSnackBar("Please upload data first!", '', 'red-snackbar');
    }
    else{
      this.projectService.uploadMinistry(this.uploadDataSource).subscribe(
        data => {
          console.log(data);
          this.dataSource.data = [];
          this.uploadDataSource = null;
          this.openSnackBar(data.message, '', 'blue-snackbar');
        },
        error => {
          console.log(error);
          this.openSnackBar(error.error, '', 'red-snackbar');
        }
      );
    }



  }

  openSnackBar(message: string, action: string, className: string) {
    this.snackBar.open(message, action,
      {
        duration: 2000,
        verticalPosition: 'bottom',
        horizontalPosition: 'end',
        panelClass: [className]
      });
  }


}
