import { DocReferences } from "../../data-acquisition/model/doc-reference";

export class ProjectInfo {
    projectid: string;
    projectname: string;
    createdby: string;
    date : string;
    meetingDate : Date;
    uploadedFile : DocReferences[];
    constructor(projectId: string, projectName: string, createby: string, date : string, meetingDate: Date) {
        this.projectid = projectId;
        this.projectname = projectName;
        this.createdby = createby;
        this.date = date;
        this.meetingDate = meetingDate;
    }
}