import { Injectable } from '@angular/core';
import { HttpClient, HttpEvent, HttpRequest } from '@angular/common/http';
import { Observable } from 'rxjs';
import { NetworkConstant } from '../constants/network-constant';
 
@Injectable({
  providedIn: 'root'
})
export class UploadFileService {
 
  constructor(private http: HttpClient) { }
 
  pushFileToStorage(projectId: string, folderName: string,file: File): Observable<HttpEvent<{}>> {
    const formdata: FormData = new FormData();
 
    formdata.append('file', file);
    formdata.append('project_id',projectId);
    formdata.append('upload_folder',folderName);
 
    const req = new HttpRequest('POST', NetworkConstant.BASE_URL+NetworkConstant.UPLOAD_FILE_ENDPOINT, formdata, {
      reportProgress: true,
      responseType: 'text'
    });
 
    return this.http.request(req);
  }
}