import { Component, OnInit, ViewChild, AfterViewInit, Input } from '@angular/core';

import { AuthService } from '../../auth/auth.service';
import { SignUpInfo } from '../../auth/signup-info';
import { NgForm } from '@angular/forms';
import { MatTableDataSource, MatSort, MatPaginator, MatDialog, MatDialogConfig, MatSnackBar } from '@angular/material';
import { UserService } from '../../services/user.service';
import { ConfirmationDialogComponent } from '../confirmation-dialog/confirmation-dialog.component';

export interface Role {
  value: string;
  viewValue: string;
}

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit,AfterViewInit {
  @Input() selectedRole:any;
  @Input() name:any;
  @Input() username:any;
  @Input() email:any;
  @Input() password:any;

  signupInfo: SignUpInfo;
  isSignedUp = false;
  isSignUpFailed = false;
  loader: boolean;
  errorMessage = '';
  successMessage ='';

  public displayedColumns = ['name', 'userName', 'email', 'role', 'action'];
  public dataSource = new MatTableDataSource<SignUpInfo>();
  public users:SignUpInfo[];

  @ViewChild(MatSort) sort: MatSort;
  @ViewChild(MatPaginator) paginator: MatPaginator;

  roles: Role[] = [
    {value: 'operator', viewValue: 'Operator'},
    {value: 'officer', viewValue: 'Officer'},
    {value: 'admin', viewValue: 'Admin'}
  ];

  constructor(
    private authService: AuthService,
    private userService: UserService,
    public dialog: MatDialog,
    private snackBar: MatSnackBar){
      this.loader = false;
    }

  ngOnInit() {
    this.signupInfo = new SignUpInfo('','','','','');
    this.getAllUser();
  }

  ngAfterViewInit(): void {
    this.dataSource.sort = this.sort;
    this.dataSource.paginator = this.paginator;
  }

  public getAllUser(){
    this.userService.getUserList().subscribe(res => {
      this.dataSource.data = res as SignUpInfo[];
    });
  }

  public redirectToUpdate = (id: string) => {
    console.log(this.dataSource.data[id]);

    this.signupInfo = new SignUpInfo(
      this.dataSource.data[id].name,
      this.dataSource.data[id].username,
      this.dataSource.data[id].email,
      this.dataSource.data[id].password,
      this.dataSource.data[id].roles[0].name);

  }
 
  public redirectToDelete = (userName: string) => {
    const dialogConfig = new MatDialogConfig();
    dialogConfig.disableClose = true;
    dialogConfig.autoFocus = true;
    dialogConfig.data = {
      id: 1,
      title: 'Warning!',
      body:'Do you wish to delete this user?',
    };
    const dialogRef = this.dialog.open(ConfirmationDialogComponent, dialogConfig);
    dialogRef.afterClosed().subscribe(result => {
      console.log('Dialog was closed')
      console.log(result)
      if(result){
        this.userService.deleteUser(userName).subscribe(
          data=>{
            this.openSnackBar(data.message, 'Ok', 'blue-snackbar');
            this.getAllUser();
          },
          error => {
            this.openSnackBar(error.message, 'Ok', 'red-snackbar');
          }
        )
      }
    });
  }

  public doFilter = (value: string) => {
    this.dataSource.filter = value.trim().toLocaleLowerCase();
  }

  public onSubmit(f:NgForm) {

    this.loader = true;

    this.signupInfo = new SignUpInfo(
      this.name,
      this.username,
      this.email,
      this.password,
      this.selectedRole);

      //console.log(f.controls['name'].value);

    this.authService.signUp(this.signupInfo).subscribe(
      data => {
        console.log(data);
        this.loader = false;
        this.isSignedUp = true;
        this.isSignUpFailed = false;
        this.openSnackBar(data.message, 'Ok', 'blue-snackbar');
        this.getAllUser();
        f.reset();
      },
      error => {
        console.log(error);
        this.isSignUpFailed = true;
        this.loader = false;
        this.openSnackBar(error.error, '', 'red-snackbar');
      }
    );
  }

  openSnackBar(message: string, action: string, className: string) {
    this.snackBar.open(message, action,
      {
        duration: 2000,
        verticalPosition: 'bottom',
        horizontalPosition: 'end',
        panelClass: [className]
      });
  }
}
