export class ParseDataPostModel {
    project_name: string;
    project_id: string;
    folder_name: string;
    file_name: string;

    constructor(project_name: string, project_id: string, folder_name: string, file_name: string) {
        this.project_name = project_name;
        this.project_id = project_id;
        this.folder_name = folder_name;
        this.file_name = file_name;
    }
}