import { Component, OnInit, ViewChild, Inject } from '@angular/core';
import { PdfViewerComponent, PDFSource, PDFDocumentProxy, PDFProgressData } from 'ng2-pdf-viewer';
import { ActivatedRoute } from '@angular/router';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material';

@Component({
  selector: 'app-file-viewer',
  templateUrl: './file-viewer.component.html',
  styleUrls: ['./file-viewer.component.css']
})
export class FileViewerComponent implements OnInit {

  pdfSrc: string | PDFSource | ArrayBuffer;
  error: any;
  page = 1;
  rotation = 0;
  zoom = 1.0;
  originalSize = false;
  progressData: PDFProgressData;
  pdf: any;
  renderText = true;
  isLoaded = false;
  showAll = true;
  autoresize = false;
  fitToPage = true;
  outline: any[];
  isOutlineShown = false;
  pdfQuery = '';
  @ViewChild(PdfViewerComponent) private pdfComponent: PdfViewerComponent;

  progressing: Boolean;
 
  constructor(
    private route: ActivatedRoute,
    public dialogRef: MatDialogRef<PdfViewerComponent>,
    @Inject(MAT_DIALOG_DATA) data) { 

      this.pdfSrc = data.fileUrl;
      this.progressing = true;
  }

  ngOnInit() {

    this.route.params.subscribe(data =>{

      console.log(data);
    });
  }

  public previewFile(){
    //this.pdfSrc = this.selectedDocFile.fileDownloadUri;
  }

  /**
   * Render PDF preview on selecting file
   */
  public onFileSelected() {
    const $pdf: any = document.querySelector('#file');

    if (typeof FileReader !== 'undefined') {
      const reader = new FileReader();

      reader.onload = (e: any) => {
        //this.pdfSrc = e.target.result;
        //this.pdfSrc = "https://v2.convertapi.com/d/45a894be4862ded787e68b3bbd42a3f0/Asset%20Insurance.pdf";
      };
      reader.readAsArrayBuffer($pdf.files[0]);
    }
  }

  public incrementPage(amount: number) {
    this.page += amount;
  }

  public incrementZoom(amount: number) {
    this.zoom += amount;
  }

  public gotoDestinationPage(pageNo: number) {
    this.page = pageNo;
  }
  /**
   * Get pdf information after it's loaded
   * @param pdf
   */
  public afterLoadComplete(pdf: PDFDocumentProxy) {
    this.pdf = pdf;
    this.isLoaded = true;
    this.progressing = true;
    this.loadOutline();
  }

  /**
   * Get outline
   */
  public loadOutline() {
    this.pdf.getOutline().then((outline: any[]) => {
      this.outline = outline;
    });
  }

  /**
   * Pdf loading progress callback
   * @param {PDFProgressData} progressData
   */
  public onProgress(progressData: PDFProgressData) {
    console.log(progressData);
    this.progressData = progressData;
    this.isLoaded = false;
    this.error = null; // clear error
  }

  public getInt(value: number): number {
    return Math.round(value);
  }

  /**
   * Navigate to destination
   * @param destination
   */
  public navigateTo(destination: any) {
    this.pdfComponent.pdfLinkService.navigateTo(destination);
  }

  /**
   * Scroll view
   */
  public scrollToPage() {
    this.pdfComponent.pdfViewer.scrollPageIntoView({
      pageNumber: 3
    });
  }

  /**
   * Page rendered callback, which is called when a page is rendered (called multiple times)
   *
   * @param {CustomEvent} e
   */
  public pageRendered(e: CustomEvent) {
    console.log('(page-rendered)', e);
  }

  public searchQueryChanged(newQuery: string) {
    //if (newQuery !== this.pdfQuery) {
    this.pdfQuery = newQuery;
    this.pdfComponent.pdfFindController.executeCommand('find', {
      query: this.pdfQuery,
      highlightAll: true,
      caseSensitive: false,
      findPrevious: undefined,
      phraseSearch: true
    });
    /* } else {
      this.pdfComponent.pdfFindController.executeCommand('findagain', {
        query: this.pdfQuery,
        highlightAll: true,
        caseSensitive: false,
        findPrevious: undefined,
        phraseSearch: true
      });
    } */
  }
  /**
   * Handle error callback
   *
   * @param error
   */
  onError(error: any) {
    this.error = error; // set error
  }

  onNoClick(): void {
    this.dialogRef.close();
  }


}
